$menu-cook-popper-bg: #fff !default;
$menu-cook-popper-box-shadow: 0 1px 6px 0 rgba(51, 51, 51, 0.15) !default;

$menu-cook-item-width: 208px !default;
$menu-cook-item-space: 12px !default;

$menu-cook-popper-scrollbar-width: 8px !default;
$menu-cook-popper-padding: 12px 24px !default;
$menu-cook-popper-single-padding: 12px 24px !default;
$menu-cook-popper-height: 480px !default;
$menu-cook-popper-width: 696px !default;
$menu-cook-popper-space: 24px !default;
$menu-cook-side-padding: $menu-cook-popper-space * 2 !default;

$menu-cook-search-active-color: #4686f2 !default;
$menu-cook-search-space-top: 16px !default;
$menu-cook-search-padding: $menu-cook-search-space-top 24px 0 !default;
$menu-cook-search-input-border: 1px solid #ededed !default;
$menu-cook-search-input-height: 24px !default;
$menu-cook-search-input-padding: 0 16px + 8px !default;
$menu-cook-search-height: $menu-cook-search-space-top + $menu-cook-search-input-height !default;
$menu-cook-search-icon-color: #333 !default;
$menu-cook-search-input-color: #333 !default;
$menu-cook-popper-padding-has-search: (12px + $menu-cook-search-height) 24px 12px !default;

$menu-cook-search-placeholder-color: #999 !default;
$menu-cook-search-empty-color: #999 !default;
$menu-cook-search-empty-query-color: #333 !default;
$menu-cook-search-empty-query-bg: #f7f7f7 !default;

$menu-cook-tabs-space-top: 12px !default;
$menu-cook-tabs-padding: $menu-cook-tabs-space-top 24px 0 !default;
$menu-cook-tabs-height: 28px !default;
$menu-cook-tabs-wrap-height: $menu-cook-tabs-space-top + $menu-cook-tabs-height !default;

@include b(menu-cook-popper) {
  $p: '.#{$namespace}-menu-cook';
  display: flex;
  background: $menu-cook-popper-bg;
  // box-shadow: 10px 10px 8px -6px rgba(24, 23, 23, 0.15);
  box-shadow: $menu-cook-popper-box-shadow;
  border-radius: 2px;
  max-height: $menu-cook-popper-height;

  @include when(has-search){
    #{$p}-wrap {
      position: relative;
      padding: $menu-cook-search-height 0 0;
    }
  }

  @include when(full) {
    box-shadow: none;
    height: 100%;
    max-height: 100%;

    &.is-has-tabs {
      #{$p}-wrap {
        position: relative;
        height: 100%;
        max-height: 100%;
        padding: $menu-cook-search-height + $menu-cook-tabs-wrap-height 0 0;
      }
    }

    #{$p}-tabs-wrap {
      position: absolute;
      width: 100%;
      top: $menu-cook-search-height;
      left: 0;
      padding: $menu-cook-tabs-padding;
    }

    #{$p}-wrap {
      position: relative;
      height: 100%;
      padding: $menu-cook-search-height 0 0;
    }
  }
}

@include b(menu-cook-wrap){
  position: relative;
  max-height:$menu-cook-popper-height;
}

@include b(menu-cook-search) {
  $p: '.#{$B}';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: $menu-cook-search-padding;

  #{$p}:hover,
  #{$p}.is-focused {
    @include e(icon) {
      color: $menu-cook-search-active-color;
    }
  }

  @include e(icon) {
    position: absolute;
    color: $menu-cook-search-icon-color;
  }
  @include e(input) {
    display: block;
    width: 100%;
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: $menu-cook-search-input-border;
    height: $menu-cook-search-input-height;
    padding: $menu-cook-search-input-padding;
    color: $menu-cook-search-input-color;
    background: transparent;
    box-sizing: border-box;
    transition: all 0.4s ease-in-out;
    @include ellipsis();
    &:hover,
    &:focus {
      outline: none;
    }

    &::placeholder {
      color: $menu-cook-search-placeholder-color;
    }

    // &:focus {
    //   border-bottom: 1px solid #4686f2;
    // }
  }
}

@include b(menu-cook-body) {
  //width: $menu-cook-item-width + $menu-cook-side-padding;
  // max-height: $menu-cook-popper-height;
  height: 100%;
  padding: $menu-cook-popper-padding;

  //@for $i from 1 through 4 {
  //  &.columns-#{$i} {
  //    width: $menu-cook-item-width *
  //      $i +
  //      $menu-cook-item-space *
  //      ($i - 1) +
  //      $menu-cook-side-padding;
  //  }
  //}
}

@include b(menu-cook-content) {
  width: 648px + $menu-cook-popper-scrollbar-width;
  height: 100%;
  box-sizing: border-box;
  overflow-y: hidden;
  overflow-x: hidden;
  padding-right: $menu-cook-popper-scrollbar-width;
  margin-right: -$menu-cook-popper-scrollbar-width;
  &:hover {
    overflow-y: auto;
  }

  @for $i from 1 through 4 {
    &.columns-#{$i} {
      width: $menu-cook-item-width *
        $i +
        $menu-cook-item-space *
        ($i - 1) +
        $menu-cook-popper-scrollbar-width;
    }
  }

  @include m(inner) {
    width: 648px;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;

    @for $i from 1 through 4 {
      &.columns-#{$i} {
        width: $menu-cook-item-width * $i + $menu-cook-item-space * ($i - 1);
      }
    }

    @include when(horizontal) {
      display: block;
    }
  }
}

@include b(menu-cook-column) {
  flex: 0 0 auto;
  align-self: baseline;
  padding: 0 $menu-cook-item-space / 2;
}

@include b(menu-cook-group) {
  margin-bottom: 24px;
  padding: 0 0 8px;
  &:last-child {
    margin-bottom: 0;
  }

  @include when(horizontal) {
    display: flex;
    flex-wrap: wrap;

    // cook-item title 占满整行
    @include b(menu-cook-item) {
      @include when(title) {
        width: 100%;
        justify-content: flex-start;

        @include b(menu-cook-item-star) {
          margin-left: 5px;
        }
      }
    }
  }
}

@include b(menu-cook-side) {
  width: 132px + 8px;
  // max-height: $menu-cook-popper-height + 36px;
  padding: 42px 24px 12px 0;
}

@include b(menu-cook-empty) {
  color: $menu-cook-search-empty-color;
  height: 20px;
  line-height: 20px;
  @include e(query) {
    color: $menu-cook-search-empty-query-color;
    background: $menu-cook-search-empty-query-bg;
    padding: 0 3px;
  }
}
