/* select-var 
---------------------------*/
@import './var.scss';

/* 组件通用样式
---------------------------*/
@import '~@hui/scss-utils';

// TODO
// poptip 样式拆分
// checkbox 样式拆分
@import './components/checkbox.scss';
@import './components/poptip.scss';

/* select bem
---------------------------*/
@include b(select) {
  $select: &;

  display: inline-block;
  width: 100%;
  padding: 0 $select-box-shadow-width;
  box-sizing: border-box;
  vertical-align: $select-vertical-align;
  color: $select-text-color;
  font-size: $select-font-size-base;
  height: $select-height-default;
  position: relative;

  // TODO 自适应高度
  &.adapt-height {
    min-height: $select-height-default;
    height: auto;
  }

  @include e(head) {
    // TODO head-padding
    padding-left: $select-padding-base - $select-box-shadow-width;
    padding-right: $select-padding-base + $select-head-icon-size - $select-box-shadow-width;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    align-content: flex-end;

    @include se(right-icon) {
      position: absolute;
      top: 50%;
      right: $select-padding-base;
      line-height: 1;
      margin-top: -($select-head-icon-size / 2) + 1px;
      font-size: $select-head-icon-size;
      color: $select-head-icon-color;
      transition: all 0.2s ease-in-out;
    }

    @include se(arrow) {
      right: $select-icon-arrow-right;
    }

    @include se(clear) {
      font-size: $select-icon-clear-size;
      margin-top: -$select-icon-clear-size / 2;
      right: $select-icon-clear-right;
    }

    @include se(loading) {
      right: $select-icon-arrow-right;
    }

    @include when(clearable) {
      padding-right: $select-icon-clear-size + $select-icon-clear-right;
    }

    @include when(multiple) {
      padding-left: $select-tag-space;
    }
  }

  @include e(selection) {
    display: block;
    box-sizing: border-box;
    height: $select-height-default;
    line-height: $select-height-default;
    outline: none;
    user-select: none;
    cursor: pointer;
    background-color: $select-head-bg-default;
    border-radius: $select-head-border-radius;
    border: $select-head-border;
    transition: all 0.2s ease-in-out;

    &:hover {
      @include hover();
    }
  }

  @include e(tag) {
    $margin: ($select-height-default - $select-tag-height - $select-border-width * 2)/2;
    position: relative;
    box-sizing: border-box;
    margin: $margin $select-tag-space $margin 0;
    max-width: $select-tag-width-max;
    height: $select-tag-height;
    line-height: $select-tag-height;
    padding-right: $select-tag-padding-right;
    padding-left: $select-tag-padding-left;
    background: $select-tag-background;
    border: $select-tag-border;
    border-radius: $select-tag-border-radius;
    font-size: $select-tag-font-size;
    @include ellipsis();

    @include when(more) {
      padding-right: $select-tag-space;
      padding-left: $select-tag-space;
    }

    @include m(text) {
      display: inline-block;
      width: 100%;
      @include ellipsis();
    }

    i[name='close'] {
      position: absolute;
      right: $select-tag-space;
      font-size: $select-tag-font-size;
      color: $select-tag-close-color;
      cursor: pointer;
    }

    // todo 文本 tag
    &.#{$namespace}-text-tag {
      display: inline-block;
      border: none !important;
      border-radius: 0;
      margin: 0;
      background: transparent !important;
      padding-right: 2px;

      &:focus {
        outline: none;
        background: #bdddfb !important;
      }

      .#{$namespace}-text-tag-max {
        background: #34c3fe;
        color: #fff;
        border-radius: 10px;
        padding: 0 6px;
      }
    }
  }

  @include e(text-tags) {
    display: block;
    height: 100%;
    width: 100%;
    font-size: $select-tag-font-size;
    color: $select-text-color;
    @include ellipsis();
  }

  @include e(input) {
    display: inline-block;
    max-width: 100%;
    height: $select-height-default - 2px; // 多选下 input 会把 head 撑开，临时处理
    line-height: $select-height-default - 2px;
    flex-grow: 1;
    box-sizing: border-box;
    padding: $select-head-input-padding;
    font-size: $select-head-input-font-size;
    vertical-align: top;
    outline: none;
    border: none;
    color: $select-head-input-color;
    background-color: transparent;
    position: relative;
    cursor: pointer;
    @include placeholder();
    @include ellipsis();
    &::placeholder {
      color: $select-placeholder-color;
      font-size: $select-placeholder-font-size;
    }
  }

  @include share-rule(value) {
    display: block;
    height: $select-height-default - $select-box-shadow-width;
    line-height: $select-height-default - $select-box-shadow-width;
    width: 100%;
    font-size: $select-head-font-size;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  @include b(select-placeholder) {
    @include extend-rule(value);
    color: $select-placeholder-color;
    font-size: $select-placeholder-font-size;
  }

  @include b(select-selected-value) {
    @include extend-rule(value);
    color: $select-text-color;
    font-size: $select-placeholder-font-size;
  }

  @include hui-when(visible) {
    @include b(select-selection) {
      @include active-border($select-primary-color, $select-box-shadow-width);
      z-index: 10;
    }
    @include b(select-head-arrow) {
      transform: rotate(180deg) translateY(1px);
    }
  }

  @include hui-when(disabled) {
    @include b(select-selection) {
      @include disabled();
    }

    @include b(select-input) {
      cursor: not-allowed;
    }
  }

  @include hui-when(readonly) {
    @include b(select-selection) {
      @include readonly();
    }

    @include b(select-input) {
      cursor: default;
    }
  }

  @include hui-when(single) {
    position: relative;
  }

  @include hui-when(multiple) {
    &.adapt-height {
      #{$select}-selection {
        min-height: $input-height-base;
        height: auto;
      }
    }
  }

  @include hui-when(small) {
    border-radius: 3px;
    @include size($select, $select-height-small);
  }

  @include hui-when(large) {
    @include size($select, $select-height-large, $font-size-base);
  }

  @include hui-when(left) {
    @include align($select, left);
  }

  @include hui-when(center) {
    @include align($select, center);
  }

  @include hui-when(right) {
    @include align($select, right);
  }
}

@include b(select-item) {
  position: relative;
  display: flex;
  max-width: 100%;
  height: $select-option-height;
  line-height: $select-option-height;
  margin: 0;
  padding: 0 $select-padding-base;
  clear: both;
  color: $select-option-font-color;
  font-size: $select-option-font-size;
  white-space: nowrap;
  list-style: none;
  cursor: pointer;
  @include ellipsis();

  $selector: &;

  @include when(slot) {
    display: block;
  }

  @include hui-when(focus) {
    background: $select-option-hover-color;
  }

  @include hui-when(disabled) {
    color: $select-option-font-color-disabled;
    cursor: not-allowed;
  }

  @include hui-when(selected) {
    $s: &;
    // 多选时不添加背景色
    @at-root {
      .is-multiple {
        #{$s} {
          background: transparent;
        }
        #{$s}#{$selector}-focus {
          background: $select-option-hover-color;
        }
      }

      .is-overage {
        #{$s} {
          cursor: pointer;
        }
      }
    }
    color: $select-option-selected-color;
    background: $select-option-selected-bg;
  }

  // TODO  dvivided ?
  @include hui-when(divided) {
    color: $select-option-selected-color;
    margin-top: 5px;
    border-top: 1px solid $border-color-split;
    &:before {
      content: '';
      height: 5px;
      display: block;
      margin: 0 -16px;
      background-color: #fff;
      position: relative;
      top: -7px;
    }
  }

  &:hover {
    background: $select-option-hover-color;
  }

  // select query 匹配字段高亮
  @include e(hlight) {
    color: $select-option-hlight;
    font-weight: bold;
  }

  @include e(checkbox) {
    position: absolute;
    padding: 0;
    input {
      display: none;
    }
  }

  @include e(inner) {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;

    @include when(checkbox) {
      padding-left: 14px + $select-padding-base;
    }
  }

  @at-root {
    .is-overage {
      #{$selector} {
        cursor: not-allowed;
      }
    }
  }
}

@include b(select-item-text) {
  display: inline-block;
  @include with-count(1) {
    width: 100%;
    max-width: 100%;
    @include ellipsis();
  }

  @include with-count(2) {
    width: 50%;
    max-width: 50%;
    @include ellipsis();
  }
}

@include b(select-group) {
  list-style: none;
  margin: 0;
  padding: 0;

  @include e(title) {
    padding-left: $select-padding-base;
    font-size: 12px;
    color: $select-group-text-color;
    height: $select-option-height;
    line-height: $select-option-height;
  }

  @include e(content) {
    padding-left: $select-padding-base;
  }
}

@include b(select-dropdown) {
  position: absolute;
  width: inherit;
  margin: $select-dropdown-margin;
  background-color: $select-dropdown-background-color;
  box-sizing: border-box;
  border-radius: 4px;
  color: $select-dropdown-font-color;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
  z-index: $select-dropdown-zindex;

  @include hui-when(transfer) {
    z-index: $select-dropdown-zindex + 100;
  }

  @include e(content) {
    max-height: $select-dropdown-height-max;
    margin: $select-dropdown-padding 0;
    overflow: auto;
    @include select-scroll-bar();
    // &:hover {
    //   overflow: auto;
    // }
  }

  @include e(content-nolimit) {
    max-height: none;
  }

  @include e(not-found) {
    text-align: center;
  }

  @include e(no-more) {
    text-align: center;
  }

  @include e(loading) {
    text-align: center;
    @include b(select-loading) {
      animation: load 1s linear infinite;
      @keyframes load {
        from {
          transform: rotate(0deg);
        }
        to {
          transform: rotate(360deg);
        }
      }
    }
  }
}

@include b(select-loading) {
  display: inline-block;
  animation: load 1s linear infinite;
  @keyframes load {
    from {
      transform-origin: center;
      transform: rotate(0deg);
    }
    to {
      transform-origin: center;
      transform: rotate(360deg);
    }
  }
}

@include b(select-ctrl-check-all) {
  display: flex;
  justify-content: flex-end;
  border-top: 1px solid $select-border-color;
  padding: 5px;

  &.is-head {
    margin-top: 8px;
    margin-bottom: -8px;
    border-bottom: 1px solid $select-border-color;
  }
}

@include b(select-ctrl-create-option) {
  display: flex;
  justify-content: flex-end;
  border-top: 1px solid $select-border-color;
  padding: 5px;

  &.is-head {
    margin-top: 8px;
    margin-bottom: -8px;
    border-bottom: 1px solid $select-border-color;
  }
}

@include b(select-tag-more-poptip) {
  .#{$namespace}-poptip-body {
    padding: 0;
  }

  .#{$namespace}-select.#{$namespace}-select-tag-pop-wrap {
    display: flex;
    flex-wrap: wrap;
    height: auto;
    max-width: 196px;
    max-height: 500px;
    padding: $select-padding-base;
    @include select-scroll-bar();
  }
}
