// Prefix
$css-prefix: u-;
$css-prefix-iconfont: font-icon;
// Color
$primary-color: #298DFF;
$info-color: #298DFF;
$success-color: #1ABE6B;
$warning-color: #FF9901 ;
$error-color: #F14C5D;
$require-color:#EF000C;
$transparent-color: transparent;
$changed-color: #d9534f;
$link-color: #298DFF;
$link-hover-color: lighten($link-color, 20%);
$link-active-color: darken($link-color, 5%);
$selected-color: lighten($primary-color, 10%);
$no-select-color:#e5ebf1;
$tooltip-color: #fff;
$subsidiary-color: #c3cbd6;
$arrow-color: #9EA7B4;
$rate-star-color: #f5a623;
$icon-color:#9EA7B4;
$dragover-color: #b4cffd;
// Base
$body-background: #fff;
$font-family:
"Helvetica Neue",
Helvetica,
"PingFangSC-Regular",
"Hiragino Sans GB",
"Microsoft YaHei",
"微软雅黑",
Arial,
sans-serif;
$code-family: Consolas,
Menlo,
Courier,
monospace;
$title-color: #495060;
$text-color: #333333;
$font-size-base: 14px;
$font-size-small: 12px;
$line-height-base: 1.5;
$line-height-computed: floor(($font-size-base * $line-height-base));
$border-radius-base: 6px;
$border-radius-small: 4px;
$cursor-disabled: not-allowed;
// Border color
$border-color-base: #D7DDE4; // outside
$border-color-split: #F7F7F7; // inside
$border-color-bottom: #DCE1E7;
// Background color
$background-color-base: #f7f7f7; // base
$background-color-select-hover: #EAF5FF;



$form-cols: 12;
$form-component-height:32px;

$max-height: 210px;

//form
$form-label-color: $text-color; 
//$form-item-label-text-align:right;
$form-item-label-background:transparent;
$form-item-label-padding: 0 12px 0 0;
$form-item-labelWrap-padding:0 12px 0 0;
$form-item-margin-bottom:24px;
$form-label-top-padding:0 0 10px 0;
$form-inline-margin-right:10px;
$form-row-form-item-padding:5px;
$form-row-form-item-margin:0;
$form-row-required-left:-3px;
$form-row-required-requiredIcon-left:-9px;

$form-error-tip-left:0;
$form-error-tip-line-height:1;
$form-error-tip-padding-top:6px;

$form-required-label-before-margin-right:4px;
$form-required-item-requiredIcon-left:-9px;
$form-item-h-input-padding:0 12px;



//mixin
$mixin-content-header-color:$title-color;
$mixin-checkbox-disabled-check-color:#ccc;
$mixin-input-disabled-color:#999;
$mixin-checkbox-inner-border-color:#D7DDE4;
$mixin-checkbox-disabled-color:#f3f3f3;
$mixin-checkbox-disabled-bg:$border-color-split;
$mixin-checkbox-disabled-check-border-color:$border-color-base;

// Input
$input-height-small: 24px;

