// Prefix
$css-prefix: u-;
$css-prefix-iconfont: font-icon;
// Color
$primary-color: #4686F2;
$info-color: #298DFF;
$success-color: #1ABE6B;
$warning-color: #FF9901 ;
$error-color: #F5222D;
$transparent-color: transparent;
$changed-color: #d9534f;
$link-color: #298DFF;
$link-hover-color: lighten($link-color, 20%);
$link-active-color: darken($link-color, 5%);
$selected-color: lighten($primary-color, 10%);
$no-select-color:#e5ebf1;
$tooltip-color: #fff;
$subsidiary-color: #c3cbd6;
$arrow-color: #9EA7B4;
$rate-star-color: #f5a623;
// $icon-color:#9EA7B4;
$icon-color:#999;
$dragover-color: #b4cffd;
// Base
$body-background: #fff;
$font-family:
"Helvetica Neue",
Helvetica,
"PingFang SC",
"Hiragino Sans GB",
"Microsoft YaHei",
"微软雅黑",
Arial,
sans-serif;
$code-family: Consolas,
Menlo,
Courier,
monospace;
$title-color: #495060;
$text-color: #495060;
$font-size-base: 16px;
// Background color
$background-color-base: #f7f7f7; // base
$background-color-select-hover: #EAF5FF;

$table-thead-bg: #fff;
$border-color-split: #F7F7F7; // inside
// $btn-disable-color: #c3cbd6;
$btn-disable-color: #BBB;
$btn-font-weight: normal;
$btn-padding-base: 6px 15px;
$btn-padding-large: 6px 15px 7px 15px;
$btn-padding-small: 5px 8px;
$btn-font-size: 12px;
$btn-font-size-large: 14px;
$btn-font-size-small:12px;
$btn-border-radius: 4px;
$btn-border-radius-small: 2px;
$btn-disable-bg: $background-color-base;
$cursor-disabled: not-allowed;
// Input
$input-height-base: 28px;
$input-height-large: 36px;
$input-height-small: 24px;
// Animation
$animation-time: .3s;
$transition-time: .2s;
$ease-in-out: ease-in-out;
