//datepicker
$date-picker-cell-margin:12px 16px 14px 16px;
$date-picker-cell-hover-bg: #EFF5FF;
$date-picker-panel-shortcut-hover-bg:#e2e7eb;
$date-picker-cell-span-width:24px;
$date-picker-cell-span-height:24px;
$date-picker-head-span-height:22px;
$date-picker-cell-span-margin:2px;
$date-picker-cells-header-span-line-height:22px;
$date-picker-cells-header-span-margin:2px;
$date-picker-cells-cell-span-width:28px;
$date-picker-cells-cell-span-height:28px;
$date-picker-cells-cell-today-width:6px;
$date-picker-cells-cell-today-height:6px;
$date-picker-cells-cell-today-top:1px;
$date-picker-cells-cell-today-right:1px;
$date-picker-cell-range-before-top:2px;
$date-picker-cell-range-before-bottom:2px;
$date-picker-cell-selected-hover-color:#fff;
$date-picker-cells-year-margin-top:14px;
$date-picker-cells-year-span-width:44px;
$date-picker-cells-year-span-height:24px;
$date-picker-cells-year-span-margin:10px 12px;
$date-picker-header-height:32px;
$date-picker-panel-width:20px;
$date-picker-panel-height:32px;
$date-picker-panel-line-height:32px;
$date-picker-panel-margin-top:4px;
$date-picker-sidebar-width:92px;
$date-picker-sidebar-margin-left:-92px;
$date-picker-sidebar-bg:$table-thead-bg;
$date-picker-boder-color: #EDEDED;


$date-picker-panel-shortcut-height:28px;

$date-picker-confirm-border-top:1px solid #DCE1E7;
$datepicker-cells-header-color:$btn-disable-color;
$datepicker-cells-prev-month-color:$btn-disable-color;
