@import '~@hui/scss-utils';
// /* BEM config */
// $namespace: 'u' !default;
// $element-separator: '-' !default;
// $modifier-separator: '-' !default;
// $state-prefix: 'is-' !default;

$anchor-primary: #4686f2 !default;

$anchor-border-width: 2px !default;
$anchor-border-color: #e6e6e6 !default;

$anchor-link-padding: 0 0 0 12px !default;
$anchor-link-height: 28px !default;
$anchor-link-color: #333 !default;
$anchor-link-font-size: 12px !default;
$anchor-link-hover-bg: transparent !default;
$anchor-link-active-color: $anchor-primary !default;
@include b(anchor) {
  $p: '.#{$B}';
  position: relative;

  @include e(link) {
    height: $anchor-link-height;
    line-height: $anchor-link-height;
    @include ellipsis();

  }

  @include e(link-title) {
    display: inline-block;
    width: 100%;
    padding: $anchor-link-padding;
    color: $anchor-link-color;
    font-size: $anchor-link-font-size;
    transition: all 0.3s;
    @include ellipsis();


    &:hover {
      color: $anchor-link-active-color;
      background: $anchor-link-hover-bg;
    }

    @include when(active) {
      color: $anchor-link-active-color;
    }
  }


  #{$p}-link #{$p}-link {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  #{$p}-ink {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    &::before {
      position: relative;
      display: block;
      width: $anchor-border-width;
      height: 100%;
      margin: 0 auto;
      background-color: $anchor-border-color;
      content: ' ';
    }

    &-ball {
      position: absolute;
      left: 50%;
      display: none;
      width: 8px;
      height: 8px;
      background-color: transparent;
      border: 2px solid $anchor-primary;
      border-radius: 8px;
      transform: translateX(-50%);
      transition: top 0.3s ease-in-out;
      &.visible {
        display: inline-block;
      }
    }

    &-line {
      position: absolute;
      left: 0;
      display: none;
      width: $anchor-border-width;
      height: $anchor-link-height;
      background-color: $anchor-primary;
      transition: top 0.3s ease-in-out;
      &.visible {
        display: inline-block;
      }
    }
  }
}

@include b(anchor-wrap) {
  width: 100%;
  height: 100%;
  overflow: hidden;

  &:hover {
    overflow-y: auto;
  }

  @include when(ball-ink) {
    margin-left: -4px;
    padding-left: 4px;
  }
}
