@import '~@hui/scss-utils';
@import './theme.scss';
@import './transition.scss';
@import './cook-item.scss';
@import './cook-popper.scss';
@import './cook-submenu.scss';

/* BEM config */
$namespace: 'u' !default;
$element-separator: '-' !default;
$modifier-separator: '-' !default;
$state-prefix: 'is-' !default;

/* custom prefix */
$menu-prefix: '.#{$namespace}-menu';
$submenu-prefix: '.#{$namespace}-submenu';

$--color-text-secondary: #909399 !default;
$--color-text-primary: #333333 !default;
$--color-primary: #4686f2 !default;
$--color-white: #fff !default;
$--color-dark: #545c64 !default;

$--border-color-light: #e4e7ed !default;
$--border-radius-small: 2px !default;
$--box-shadow-light: 0 2px 12px 0 rgba(0, 0, 0, 0.1) !default;

$menu-bg: transparent !default;
$menu-padding: 12px 0 12px !default;
$menu-collapse-width: 60px !default;

$menu-item-space: 16px !default;
$menu-item-hover: #f6f6f6 !default;
$menu-item-padding: 0 $menu-item-space !default;
$menu-item-height: 36px !default;
$menu-item-nested-height: 32px !default;

$menu-item-active-bg: #eef5fe !default;
$menu-item-active-color: $--color-primary !default;
$menu-item-active-border: $--color-primary !default;
$menu-item-active-line-color: $--color-primary !default;

$menu-item-font-size: 12px !default;
$menu-item-font-color: #333 !default;
$menu-item-icon-size: 14px !default;
$menu-item-icon-color: #333 !default;
$menu-item-icon-space: 12px !default;
$menu-item-icon-arrow-size: 12px !default;
$menu-item-icon-arrow-space: 12px !default;

$submenu-opened-color: transparent !default;
$submenu-active-color: transparent !default;
$submenu-item-active-color: $--color-primary !default;

$menu-item-horizontal-height: 40px;
$submenu-item-padding: 0 0 0 42px;

$themes: (
  default: (
    bg: $--color-white,
    color: #909399,
    active-color: $--color-primary,
  ),
  dark: (
    bg: $--color-dark,
    color: #fff,
    active-color: #ffd04b,
  ),
);

@mixin menu-item {
  position: relative;
  height: $menu-item-height;
  line-height: $menu-item-height;
  padding: $menu-item-padding;
  font-size: $menu-item-font-size;
  color: $menu-item-font-color;
  list-style: none;
  box-sizing: border-box;
  cursor: pointer;

  &:not(.is-disabled):hover,
  &:not(.is-disabled):focus {
    outline: none;
  }

  &:not(.is-disabled):not(.is-active):hover,
  &:not(.is-disabled):not(.is-active).is-hover {
    background-color: $menu-item-hover;
  }

  #{$menu-prefix}-item-wrap {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    & [data-hui-type='tooltip-ref'] {
      width: 100%;
      height: 100%;
    }
  }

  & > #{$menu-prefix}-item-icon,
  & > #{$menu-prefix}-item-wrap > #{$menu-prefix}-item-icon {
    display: inline;
    margin-right: $menu-item-icon-space;
    font-size: $menu-item-icon-size;
    color: $menu-item-icon-color;
  }

  &.is-nested {
    height: $menu-item-nested-height;
    line-height: $menu-item-nested-height;
    & > #{$menu-prefix}-item-icon,
    & > #{$menu-prefix}-item-wrap > #{$menu-prefix}-item-icon {
      margin-right: $menu-item-icon-space / 2;
    }
  }

  & > #{$menu-prefix}-item-title,
  & > #{$menu-prefix}-item-wrap > #{$menu-prefix}-item-title {
    @include ellipsis();
    opacity: 1;
  }

  & > i[name='arrow'] {
    position: absolute;
    top: 0;
    right: $menu-item-icon-arrow-space;
    text-align: center;
    font-size: $menu-item-icon-arrow-size;
    color: $menu-item-icon-color;
    vertical-align: middle;
    transition: all 0.3s ease-in-out;
  }

  @include when(disabled) {
    cursor: not-allowed;
    opacity: 0.6;
    &:hover,
    &:focus {
      outline: none;
    }
  }

  @include when(active) {
    color: $menu-item-active-color;
    background: $menu-item-active-bg;
    i {
      color: inherit;
    }
    &.has-after-line:after {
      content: '';
      display: block;
      width: 3px;
      height: 100%;
      position: absolute;
      right: 0;
      top: 0;
      background: $menu-item-active-line-color;
    }
  }

  @include when(collapse) {
    padding: 0;
    #{$menu-prefix}-item-wrap {
      padding: 0;
      text-align: center;
      justify-content: center;
    }

    & > #{$menu-prefix}-item-icon,
    #{$menu-prefix}-item-wrap > #{$menu-prefix}-item-icon {
      display: inline;
      font-size: 16px;
      margin-right: 0;
    }
  }

  @include when(horizontal) {
    @include horizontal-item();
    a,
    a:hover {
      color: inherit;
    }
  }

  @include when(nested) {
    &.nest-level-1 {
      padding: 0 10px 0 42px;
    }
    &.nest-level-2 {
      padding: 0 10px 0 62px;
    }
    &.nest-level-1#{$submenu-prefix}-title {
      padding: 0 25px 0 42px;
    }
  }
}

@mixin horizontal-item {
  float: left;
  height: $menu-item-horizontal-height;
  line-height: $menu-item-horizontal-height;
  margin: 0;
  border-bottom: 2px solid transparent;
  color: $--color-text-secondary;
}

@include b(menu) {
  $prefix: '.#{$B}';
  $sub-prefix: $submenu-prefix;

  @include themed($themes) {
    background-color: t('bg');
    &#{$prefix}-horizontal {
      border-bottom: solid 1px #e6e6e6;
    }

    & > #{$prefix}-item {
      color: t('color');
    }

    & > #{$prefix}-item.is-horizontal.is-active {
      color: t('active-color');
      border-bottom: 2px solid t('active-color');
    }

    & #{$sub-prefix}.is-active.is-horizontal > #{$sub-prefix}-title {
      color: t('active-color');
      border-bottom: 2px solid t('active-color');
    }
  }

  position: relative;
  display: block;
  margin: 0;
  list-style: none;
  background-color: $menu-bg;
  @include clearfix;
  &#{$prefix}-horizontal {
    border-bottom: solid 1px #e6e6e6;
  }

  @include m(horizontal) {
    padding: 0;
    border-right: none;
  }

  @include m(collapse) {
    width: $menu-collapse-width;
  }

  @include m(inline) {
    padding: 0;
  }
}

@include b(menu-popup) {
  z-index: 100;
  min-width: 200px;
  border: none;
  padding: 5px 0;
  border-radius: $--border-radius-small;
  box-shadow: $--box-shadow-light;
  background: #fff;

  &-bottom-start {
    margin-top: 5px;
  }
  &-right-start {
    margin-left: 5px;
    margin-right: 5px;
  }
}

@include b(menu-item) {
  @include menu-item;

  @include when(active) {
    // 只激活本层title
    & > #{$menu-prefix}-item-wrap {
      #{$menu-prefix}-item-icon,
      #{$menu-prefix}-item-title {
        color: $menu-item-active-color;
      }
    }
  }
}

@include b(submenu) {
  list-style: none;
  margin: 0;
  padding-left: 0;
  transition: all 0.3s ease-in-out;

  &:not(.is-disabled):hover,
  &:not(.is-disabled):focus {
    outline: none;
  }

  &:not(.is-nested).is-active{
    background: $submenu-active-color;
  }

  @include e(title) {
    @include menu-item;

    @include when(horizontal) {
      @include horizontal-item();
    }

    &:hover {
      background-color: $menu-item-hover;
    }
  }

  @include when(opened) {
    & > #{$submenu-prefix}-title > i[name='arrow'] {
      transform: rotateZ(-180deg);
    }

    background: $submenu-opened-color;
  }

  @include when(active) {
    #{$submenu-prefix}-title {
      border-bottom-color: $menu-item-active-color;
    }

    // 只激活本层title
    & > #{$submenu-prefix}-title {
      #{$menu-prefix}-item-icon,
      #{$menu-prefix}-item-title {
        color: $submenu-item-active-color;
      }
    }
  }

  @include when(disabled) {
    #{$submenu-prefix}-title,
    #{$menu-prefix}-item {
      opacity: 0.25;
      cursor: not-allowed;
      background: none;
    }
  }

  @include when(horizontal) {
    @include horizontal-item();
    i[name='arrow'] {
      position: relative;
      display: inline;
    }
  }

  #{$menu-prefix} {
    border: none;
  }

  #{$menu-prefix}-inline {
    // box-shadow: inset 0 2px 4px rgba(0,0,0,.45);
  }
}

@include b(menu-item-group) {
  > ul {
    padding: 0;
  }
  @include e(title) {
    padding: 7px 0 7px 20px;
    line-height: normal;
    color: $--color-text-secondary;
  }
}
