// Prefix
$css-prefix: u-;
$css-prefix-iconfont: font-icon;
// Color
$primary-color: #298DFF;
$info-color: #298DFF;
$success-color: #1ABE6B;
$warning-color: #FF9901 ;
$error-color: #F5222D;
$transparent-color: transparent;
$changed-color: #d9534f;
$link-color: #298DFF;
$link-hover-color: lighten($link-color, 20%);
$link-active-color: darken($link-color, 5%);
$selected-color: lighten($primary-color, 10%);
$no-select-color:#e5ebf1;
$tooltip-color: #fff;
$subsidiary-color: #c3cbd6;
$arrow-color: #9EA7B4;
$rate-star-color: #f5a623;
$icon-color:#9EA7B4;
$dragover-color: #b4cffd;
// Base
$body-background: #fff;
$font-family:
"Helvetica Neue",
Helvetica,
"PingFang SC",
"Hiragino Sans GB",
"Microsoft YaHei",
"微软雅黑",
Arial,
sans-serif;
$code-family: Consolas,
Menlo,
Courier,
monospace;
$title-color: #495060;
$text-color: #495060;
$btn-icon-color: #999;
$title-color: #495060;
$text-color: #495060;
$font-size-base: 14px;
$font-size-small: 12px;
$line-height-base: 1.5;
$line-height-computed: floor(($font-size-base * $line-height-base));
$border-radius-base: 6px;
$border-radius-small: 4px;
$cursor-disabled: not-allowed;
// Border color
// $border-color-base: #D7DDE4; // outside
$border-color-base: #D9D9D9; // outside
$border-color-hover: #5196FF; // outside
$border-color-split: #F7F7F7; // inside
$border-color-bottom: #DCE1E7;
// Background color
$background-color-base: #f7f7f7; // base
$background-color-select-hover: #EAF5FF;
$form-cols: 12;
$form-component-height:32px;

// Input
$input-height-base: 26px;
$input-height-large: 36px;
$input-height-small: 24px;
$input-padding-horizontal: 8px;
$input-padding-vertical-base: 4px;
$input-padding-vertical-small: 1px;
$input-padding-vertical-large: 5px;
$input-placeholder-color: #c3cbd6;
$input-color: $text-color;
$input-readonly-color:#333;
$input-border-color: $border-color-base;
$input-bg: #fff;
$input-hover-border-color: $border-color-hover;
//$input-focus-border-color: $primary-color;
$input-disabled-bg: #F7F7F7;
$input-readonly-bg: #F7F7F7;
$input-required-icon-color: #E8380C;
$input-hint: #909399;
$input-icon-width:38px;
$input-icon-normal-padding-right:32px;
$input-icon-small-width:24px;
$input-tips-padding-top:5px;
$input-tips-ul-li-max-width:100px;
$input-tips-ul-li-div-height:4px;
$input-tips-ul-li-div-margin-bottom:2px;
$input-word-limit-bottom:1px;
$input-word-limit-right:12px;
$input-icon-validate-padding-right:32px;
$input-wrapper-vertical-align:middle;

//mixin 
$mixin-content-header-color:$title-color;
$mixin-checkbox-disabled-check-color:#ccc;
$mixin-input-disabled-color:#BBB;
$mixin-checkbox-inner-border-color:#D7DDE4;
$mixin-checkbox-disabled-color:#f3f3f3;
$mixin-checkbox-disabled-bg:$border-color-split;
$mixin-checkbox-disabled-check-border-color:$border-color-base;
$mixin-input-readonly-opacity:1;
$mixin-input-border-readonly-color: mix(#fff,$input-hover-border-color, 20%);
$mixin-input-active-box-shadow: 0 0 0 2px #DCE9FF;



//fastdate
$fastdate-inner-input-padding: 0 2px;
$fastdate-inner-input-width:18px;
$fastdate-inner-split-width:20px;
$fastdate-edit-padding:0 8px;
$fastdate-inner-padding:0 48px 0 8px;
$fastdate-edit-border-radius:2px;
$fastdate-edit-border-min-width:160px;
$fastdate-activity-font-size:16px;