/* mixion function
---------------------------*/
@import './_mixin/mixins.scss';


$css-prefix: 'u-' !default;
$tooltip-prefix-cls: "#{$css-prefix}tooltip";
$tooltip-arrow: "#{$tooltip-prefix-cls}-arrow";

 
$title-color: #495060;
$text-color: #495060;
$font-size-base: 12px;
$font-size-small: 12px;
$line-height-base: 1.5;
$line-height-computed: floor(($font-size-base * $line-height-base));
$border-radius-base: 6px;
$border-radius-small: 4px;
$cursor-disabled: not-allowed;
$zindex-tooltip: 1060;
// Shadow
$shadow-color: rgba(0, 0, 0, .2);
$shadow-card: 0 1px 1px 0 rgba(0, 0, 0, .1);
$shadow-up: 0 -1px 6px $shadow-color;
$shadow-down: 0 1px 6px $shadow-color;
$shadow-left: -1px 0 6px $shadow-color;
$shadow-right: 1px 0 6px $shadow-color;
$shadow-base: $shadow-down;
//tooltip
$tooltip-bg: #585E6A;
$tooltip-color: #fff;
$tooltip-inner-font-size:$font-size-small;
$tooltip-inner-min-height:34px;
$tooltip-inner-padding:8px 12px;
$tooltip-max-width: 250px;
$tooltip-arrow-width: 5px;
$tooltip-distance: $tooltip-arrow-width - 1 + 4;
$tooltip-arrow-height:0;

.#{$tooltip-prefix-cls} {
  display: inline-block;

  &-rel{
    display: inline-block;
    position: relative;
  }

  &-popper{
    @include popper($tooltip-arrow, $tooltip-arrow-width, $tooltip-distance, $tooltip-bg);
    &[aria-hidden='true'] {
      visibility: hidden;
      opacity: 0;
      &:not(.no-transition) {
        transition: opacity .3s, visibility .3s;
      }
    }
  
    &[aria-hidden='false'] {
      visibility: visible;
      opacity: 1;
      &:not(.no-transition) {
        transition: opacity .3s;
      }
    }
  }

  &-inner{
    // max-width: $tooltip-max-width;
    min-height: $tooltip-inner-min-height;
    padding:$tooltip-inner-padding;
    color: $tooltip-color;
    font-size:$tooltip-inner-font-size;
    text-align: left;
    text-decoration: none;
    background-color: $tooltip-bg;
    border-radius: $border-radius-small;
    box-shadow: $shadow-base;
    white-space: nowrap;
  }

  &-arrow{
    position: absolute;
    width: 0;
    height:$tooltip-arrow-height;
    border-color: transparent;
    border-style: solid;
  }
}