/* 组件通用样式
---------------------------*/
@import '~@hui/scss-utils/reset';
@import '~@hui/scss-utils';
/* date-var 
---------------------------*/

@import './var.scss';


@import './mixin.scss';



$form-prefix-cls: "#{$css-prefix}form";
$form-item-prefix-cls: "#{$form-prefix-cls}-item";
$editgird-prefix-cls: "#{$css-prefix}editgird";
$fastdate-prefix-cls: "#{$css-prefix}fast-date";
$select-dropdown-prefix-cls: "#{$css-prefix}select-dropdown";


.#{$fastdate-prefix-cls} {
  &-wrapper{
    width:100%;
    display: inline-block;
    vertical-align: middle;
    height: $form-component-height;
  }
  &-inner{
    position: relative;
    width:100%;
    border:1px solid $border-color-base;
    border-radius: $fastdate-edit-border-radius;
    padding: $fastdate-inner-padding;
    min-width:$fastdate-edit-border-min-width;
    //height: $input-height-base;
    -webkit-border-radius: $fastdate-edit-border-radius;
    -moz-border-radius: $fastdate-edit-border-radius;
    -ms-border-radius: $fastdate-edit-border-radius;
    -o-border-radius: $fastdate-edit-border-radius;
    &:hover {
      @include hover($border-color-hover);
    }
    div{
      height: $input-height-base;
      line-height:$input-height-base;
      // float:left;
      span{
        display:inner-block;
        input{
          border:none;
          padding: $fastdate-inner-input-padding;
          width:$fastdate-inner-input-width;
          height:$input-height-base;
          outline:none;
          background:transparent;
          text-align:right;
          @include placeholder();
        }
        .#{$fastdate-prefix-cls}-input-disabled-cell {
          padding: $fastdate-inner-input-padding;
          width:$fastdate-inner-input-width;
          height:$input-height-base;
          text-align:right;
          background:transparent;
          user-select: none;
          outline:none;
          border:none;
          @include placeholder();
        }
        .#{$fastdate-prefix-cls}-algin-left{
          text-align:left;
        }
      }
    }
    &-single{
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
      align-items: center;
      .#{$fastdate-prefix-cls}-single{
        width:100%;
      }
    }
    .#{$fastdate-prefix-cls}-inner-split{
      width:$fastdate-inner-split-width;
      text-align:center;
    }
    .#{$fastdate-prefix-cls}-disabled-year {
      display: inline-block;
    }
    &-wrap{
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
      align-items: center;
      // .#{$fastdate-prefix-cls}-single{
      //   width:calc(50% - 10px);
      // }
      
      // .#{$fastdate-prefix-cls}-range{
      //   width:calc(50% - 10px);
      // }
    }
    .#{$select-dropdown-prefix-cls} {
      width: auto;
      padding: 0;
      overflow: visible;
      max-height: none;
    }

  }
  &-input-split {
    user-select: none;
  }
  &-arrow{
    font-size: 16px;
    transition: all 0.2s ease-in-out;
    color: $btn-icon-color;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    right: 8px;
    border: none;
    // padding: 0 2px;
    height: 26px;
    outline: none;
    background: transparent;
  } 
  // &-disabled{
  //   @include disabled();
  // }
  // &-readonly{
  //   @include readonly();
  // }
  &-no-select {
    user-select: none;
    padding: 0 2px;
  }
  &-disabled,
  fieldset[disabled], &-disabled input {
    @include disabled();
  }

  &-readonly,
  fieldset[readonly], &-readonly input{
    @include readonly();
  }

  &-visible {
    @include active();
  }
  &-edit{
    padding: 0;
    // border-radius: $fastdate-edit-border-radius;
    min-width:76px;
    border: none;
    // border:1px solid $border-color-base;
    
  }
  &-clear{
    font-size:$fastdate-activity-font-size;
    color: #BBB
  }
  &-calendar{
    font-size:$fastdate-activity-font-size;
    margin-left: 4px;
  }
    
}
.#{$form-item-prefix-cls}-error{
  .#{$fastdate-prefix-cls}{
    &-inner{
      border: 1px solid $error-color;
    }
    &-calendar{
      color: $error-color;
    }
  }
}
.#{$form-item-prefix-cls}-changed{
  .#{$fastdate-prefix-cls}{
    input{
      @include changed();
    }
    &-arrow{
      color: $input-color;
    }
  }
}
.#{$form-item-prefix-cls}-error{
  .#{$fastdate-prefix-cls}{
    &-inner{
      border: 1px solid $error-color;
    }
    &-arrow{
      color: $error-color;
    }
  }
}
.#{$editgird-prefix-cls}-cell-error{
  .#{$fastdate-prefix-cls}{
    &-inner{
      border: 1px solid $error-color;
    }
  }
}