$menu-prefix: '.#{$namespace}-menu';
$submenu-prefix: '.#{$namespace}-submenu';

$cook-submenu-icon-close-hover-color: #4686F2;
@include b(menu-cook-submenu) {
  $p: '.#{$B}';
  $self: &;
  @at-root {
    #{$self}#{$submenu-prefix},
    #{$p}#{$submenu-prefix} {

      #{$menu-prefix}-item.is-nested.nest-level-1{
        padding: 0 12px 0 16px;
      }

      #{$menu-prefix}-item.is-nested {
        transition: none;
        #{$menu-prefix}-item-wrap {
          justify-content: space-between;
        }

        &:hover{
          // padding: 0 16px 0 16px;
          #{$menu-prefix}-item-icon-close {
            display: block;
          }
        }

        #{$menu-prefix}-item-icon-wrap {
          display: flex;
        }

        #{$menu-prefix}-item-icon-close {
          display: none;
          font-size: 14px;
          &:hover{
            color: $cook-submenu-icon-close-hover-color;
          }
        }

        #{$menu-prefix}-item-icon-drag {
          font-size: 14px;
          padding-left: 4px;
          cursor: move;
          &:hover{
            color: $cook-submenu-icon-close-hover-color;
          }
        }
      }

      #{$submenu-prefix}-title:not(.is-collapse) {
        #{$menu-prefix}-item-wrap {
          padding-right: 24px;
        }

        i[name='arrow'] {
          right: 26px;
        }

        i[name='more'] {
          right: 12px;
          position: absolute;
          top: 0;
          text-align: center;
          font-size: 14px;
          vertical-align: middle;
          -webkit-transition: all 0.3s ease-in-out;
          transition: all 0.3s ease-in-out;
        }
      }
    }
  }
}
