//common
@mixin placeholder($color: $input-placeholder-color) {
    // Firefox
    &::-moz-placeholder {
        color: $color;
        opacity: 1; // Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526
    }
    // Internet Explorer 10+
    &:-ms-input-placeholder {
        color: $color;
    }
    // Safari and Chrome
    &::-webkit-input-placeholder {
        color: $color;
    }
}
@mixin disabled() {
    background-color: $input-disabled-bg;
    opacity: 1;
    cursor: $cursor-disabled !important;
    color: $mixin-input-disabled-color;
    &:hover {
      @include hover($input-border-color);
    }
}
@mixin hover($color: $input-hover-border-color) {
    // border-color: mix(#fff, $color, 20%);
    border-color: $color;
}
@mixin active($color: $input-hover-border-color) {
    border-color: mix(#fff, $color, 20%);
    outline: 0;
    box-shadow: $mixin-input-active-box-shadow;
  }
@mixin hover-error() {
    border-color: $error-color;
}
@mixin readonly() {
    background-color: $input-readonly-bg;
    opacity: $mixin-input-readonly-opacity;
    color: $input-readonly-color;
    &:hover {
      border-color: $input-border-color;
    }
}
@mixin changed() {
    color: $changed-color;
}