$menu-cook-item-width: 208px !default;
$menu-cook-item-height: 32px !default;
$menu-cook-item-padding: 0 8px !default;
$menu-cook-item-hover-bg: #eff5ff !default;
$menu-cook-label-color: #333 !default;
$menu-cook-title-color: #000 !default;
$menu-cook-star-color: #d9d9d9 !default;
$menu-cook-star-active-color: #ffc103 !default;
$menu-cook-star-hover-color: #999 !default;
$menu-cook-mark-bg: #4686f2 !default;
$menu-cook-selected-color: #4686f2 !default;

@include b(menu-cook-item) {
  $prefix: '.#{$B}';
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: $menu-cook-item-width;
  height: $menu-cook-item-height;
  line-height: $menu-cook-item-height;
  padding: $menu-cook-item-padding;
  &:hover,
  &.is-focused {
    background: $menu-cook-item-hover-bg;
    #{$prefix}-star {
      display: inline;
    }
  }

  &:hover {
    #{$prefix}-star {
      opacity: 1;
      color: $menu-cook-star-hover-color;
    }
    #{$prefix}-star.is-active {
      opacity: 1;
      color: $menu-cook-star-active-color;
    }
  }

  &.is-title {
    &:hover {
      background: transparent;
    }
  }

  &.is-can-select {
    cursor: pointer;
  }

  &.is-full {
    width: 100%;
  }

  &.is-has-mark {
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 11px;
      background: $menu-cook-mark-bg;
      width: 3px;
      height: 10px;
      border-radius: 1.5px;
    }
  }

  &.is-selected {
    @include e(label) {
      color: $menu-cook-selected-color;
    }
  }

  &-label {
    color: $menu-cook-label-color;
    @include ellipsis();
    &.is-title {
      color: $menu-cook-title-color;
      font-weight: bold;
    }
  }
  &-star {
    opacity: 0;
    color: $menu-cook-star-color;
    &:hover {
      opacity: 1;
      color: $menu-cook-star-hover-color;
    }
    &.is-active {
      opacity: 1;
      color: $menu-cook-star-active-color;
    }
  }
}
