// @import '~@hui1.0/style/custom.scss';

/* mixion function
---------------------------*/
@import '~@hui/scss-utils';

$text-color: #495060;
$font-size-small: 12px;

$radio-group-button-wrapper-btnIcon-width:18px;
$radio-group-button-wrapper-btnIcon-inner-width:14px;
$radio-group-button-wrapper-btnIcon-inner-height:14px;
$radio-group-button-large-btnIcon-width:20px;
$radio-group-button-large-btnIcon-inner-width:16px;
$radio-group-button-large-btnIcon-inner-height:16px;
$radio-group-button-small-wrapper-padding: 0 12px;
$radio-group-button-small-btnIcon-inner-width:12px;
$radio-group-button-small-btnIcon-inner-height:12px;

$css-prefix: #{$namespace + $element-separator};
$radio-prefix-cls: '#{$css-prefix}radio';
$radio-group-prefix-cls: '#{$radio-prefix-cls}-group';
$radio-inner-prefix-cls: '#{$radio-prefix-cls}-inner';
$radio-group-button-prefix-cls: '#{$radio-group-prefix-cls}-button';

$form-prefix-cls: '#{$css-prefix}form';
$form-item-prefix-cls: '#{$form-prefix-cls}-item';



$primary-color: #298DFF;

$border-radius-base: 6px;
$transition-time: .2s;
$ease-in-out: ease-in-out;

$input-hover-border-color: $primary-color;

$mixin-input-active-box-shadow: 0 0 0 2px rgba($input-hover-border-color, 0.2);
$error-color: #F5222D;
$font-size-base: 14px;
$form-component-height:32px;
$radio-vertical-wrapper-height:30px;
$radio-vertical-verify-tip-margin-top:-5px;
$radio-wrapper-font-size:$font-size-small;
$radio-wrapper-margin-right:8px;
$cursor-disabled: not-allowed;
$radio-inner-width:14px;
$radio-inner-height:14px;
$radio-inner-top:0;
$radio-inner-left:0;
$radio-inner-background-color:#fff;
$border-color-base: #D7DDE4;
$radio-inner-after-width:8px;
$radio-inner-after-height:8px;
$radio-inner-after-left:2px;
$radio-inner-after-top:2px;
$radio-large-inner-width:16px;
$radio-large-inner-height:16px;
$radio-large-inner-after-width:10px;
$radio-large-inner-after-height:10px;
$radio-small-inner-width:12px;
$radio-small-inner-height:12px;
$radio-small-inner-after-width:6px;
$radio-small-inner-after-height:6px;
$radio-input-top:0;
$radio-input-bottom:0;
$radio-input-left:0;
$radio-input-right:0;
$radio-input-opacity:0;
$changed-color: #d9534f;
$radio-disabled-inner-border-color:$border-color-base;
$radio-inner-color:#f3f3f3;
$radio-inner-after-color:#ccc;
$radio-disabled-span-color:#ccc;
$radio-span-margin-left:2px;
$radio-span-margin-right:2px;
$btn-circle-size: 32px;
$radio-group-button-wrapper-margin:0;
$radio-group-button-wrapper-padding:0 16px;
$font-size-small: 12px;
$btn-default-color: $text-color;
$radio-group-button-wrapper-background:#fff;
$btn-border-radius: 4px;
$radio-group-button-wrapper-checked-color:#fff;

$background-color-base: #f7f7f7;
$radio-group-button-wrapper-disabled-color:#ccc;
$radio-group-button-wrapper-disabled-hover-color:#ccc;
$radio-group-button-wrapper-disabled-wrapper-checked-color:#fff;
$radio-group-button-wrapper-disabled-wrapper-checked-background-color:#e6e6e6;

$btn-circle-size-large: 36px;

$btn-circle-size-small: 24px;
$radio-group-button-small-wrapper-padding: 0 12px;
$btn-border-radius-small: 3px;


@mixin radio-inner() {
  width: 16px;
  height: 16px;
}

@include b(radio-group-dot) {
  $radio-dot-height: 10px;
  $p: '.#{$namespace}-radio';

  #{$p}#{$p}-checked #{$p}-inner {
    border: 1px solid $primary-color;
  }

  @include b(radio) {
    @include e(inner) {
      border-color: transparent;
      $inner: &;

      @include radio-inner();
      &::after {
        display: none;
      }
    }

    @include e(input) {
      @include radio-inner();
      &:focus {
        outline: none;
      }
    }

    @include e(inner-dot) {
      position: absolute;
      width: $radio-dot-height;
      height: $radio-dot-height;
      left: 2px;
      top: 2px;
      border-radius: $border-radius-base;
      display: table;
      border-top: 0;
      border-left: 0;
      content: ' ';
      background-color: $primary-color;
      transition: all $transition-time $ease-in-out;
    }
  }
}

@mixin active($color: $input-hover-border-color) {
  border-color: mix(#fff, $color, 20%);
  outline: 0;
  box-shadow: $mixin-input-active-box-shadow;
}

@mixin hover-error() {
  border-color: $error-color;
}

@mixin radio-error() {
  $radio-inner-prefix-cls: '#{$radio-prefix-cls}-inner';
  .#{$radio-inner-prefix-cls} {
    border: 1px solid $error-color;
  }
  &:hover {
    @include hover-error;
  }
}

.#{$radio-group-prefix-cls} {
  display: inline-block;
  padding: 0 8px;
  // font-size: $font-size-base;
  vertical-align: middle;
  // height: $form-component-height;
  &-vertical {
    .#{$radio-prefix-cls}-wrapper {
      display: block;
      height: $radio-vertical-wrapper-height;
      line-height: $radio-vertical-wrapper-height;
    }
  }
  & ~ .verify-tip.verify-bottom {
    margin-top: $radio-vertical-verify-tip-margin-top;
  }
}

// 普通状态
.#{$radio-prefix-cls}-wrapper {
  font-size: $radio-wrapper-font-size;
  vertical-align: middle;
  display: inline-block;
  // c1 repair radio and checkbox group component in form layout problem
  line-height: 1;
  position: relative;
  white-space: nowrap;
  margin-right: $radio-wrapper-margin-right;
  cursor: pointer;
  &-disabled {
    cursor: $cursor-disabled;
  }
  .iconfont {
    font-size: $font-size-base;
  }
}

.#{$radio-prefix-cls} {
  display: inline-block;
  margin-right: $radio-wrapper-margin-right;
  white-space: nowrap;
  outline: none;
  position: relative;
  line-height: 1;
  vertical-align: middle;
  cursor: pointer;
  &:hover {
    .#{$radio-inner-prefix-cls} {
      border-color: $primary-color;
    }
  }
  &-inner {
    display: inline-block;
    width: $radio-inner-width;
    height: $radio-inner-height;
    position: relative;
    top: $radio-inner-top;
    left: $radio-inner-left;
    background-color: $radio-inner-background-color;
    border: 1px solid $border-color-base;
    border-radius: 50%;
    transition: all $transition-time $ease-in-out;

    &:after {
      position: absolute;
      width: $radio-inner-after-width;
      height: $radio-inner-after-height;
      left: $radio-inner-after-left;
      top: $radio-inner-after-top;
      border-radius: $border-radius-base;
      display: table;
      border-top: 0;
      border-left: 0;
      content: ' ';
      background-color: $primary-color;
      opacity: 0;
      transition: all $transition-time $ease-in-out;
      transform: scale(0);
    }
  }
  &-large {
    font-size: $font-size-base;
    & .#{$radio-inner-prefix-cls} {
      width: $radio-large-inner-width;
      height: $radio-large-inner-height;
      &:after {
        width: $radio-large-inner-after-width;
        height: $radio-large-inner-after-height;
      }
    }
    &.#{$radio-prefix-cls}-wrapper,
    & .#{$radio-prefix-cls}-wrapper {
      font-size: $font-size-base;
    }
  }
  &-small {
    & .#{$radio-inner-prefix-cls} {
      width: $radio-small-inner-width;
      height: $radio-small-inner-height;
      &:after {
        width: $radio-small-inner-after-width;
        height: $radio-small-inner-after-height;
      }
    }
  }

  &-input {
    position: absolute;
    top: $radio-input-top;
    bottom: $radio-input-bottom;
    left: $radio-input-left;
    right: $radio-input-right;
    z-index: 1;
    opacity: $radio-input-opacity;
    cursor: pointer;
  }
  &-inner-focus {
    @include active();
  }
}

// 选中状态
.#{$radio-prefix-cls}-checked {
  .#{$radio-inner-prefix-cls} {
    border-color: $primary-color;
    &:after {
      opacity: 1;
      transform: scale(1);
      transition: all $transition-time $ease-in-out;
    }
  }
  &:hover {
    .#{$radio-inner-prefix-cls} {
      border-color: $primary-color;
    }
  }
}

.#{$form-item-prefix-cls}-changed {
  // 选中状态
  .#{$radio-prefix-cls}-checked {
    .#{$radio-inner-prefix-cls} {
      border-color: $changed-color;
      &:after {
        background: $changed-color;
      }
    }
    &:hover {
      .#{$radio-inner-prefix-cls} {
        border-color: $changed-color;
      }
    }
  }
}

.#{$radio-prefix-cls}-disabled {
  cursor: $cursor-disabled;
  .#{$radio-prefix-cls}-input {
    cursor: $cursor-disabled;
  }

  &:hover {
    .#{$radio-inner-prefix-cls} {
      border-color: $border-color-base;
    }
  }
  .#{$radio-inner-prefix-cls} {
    border-color: $radio-disabled-inner-border-color;
    background-color: $radio-inner-color;
    &:after {
      background-color: $radio-inner-after-color;
    }
  }

  .#{$radio-prefix-cls}-disabled + span {
    color: $radio-disabled-span-color;
  }
}

span.#{$radio-prefix-cls} + * {
  margin-left: $radio-span-margin-left;
  margin-right: $radio-span-margin-right;
}

// 按钮样式
.#{$radio-group-button-prefix-cls} {
  font-size: 0;
  -webkit-text-size-adjust: none;

  .#{$radio-prefix-cls} {
    width: 0;
    margin-right: 0;
  }

  .#{$radio-prefix-cls}-wrapper {
    display: inline-block;
    height: $btn-circle-size;
    line-height: $btn-circle-size - 2px;
    margin: $radio-group-button-wrapper-margin;
    padding: $radio-group-button-wrapper-padding;
    font-size: $font-size-small;
    color: $btn-default-color;
    transition: all $transition-time ease-in-out;
    cursor: pointer;
    border: 1px solid $border-color-base;
    border-left: 0;
    background: $radio-group-button-wrapper-background;

    > span {
      margin-left: 0;
    }

    &:before {
      content: '';
      position: absolute;
      width: 1px;
      height: 100%;
      left: -1px;
      background: $border-color-base;
      visibility: hidden;
      transition: all $transition-time ease-in-out;
    }

    &:first-child {
      border-radius: $btn-border-radius 0 0 $btn-border-radius;
      border-left: 1px solid $border-color-base;
      &:before {
        display: none;
      }
    }

    &:last-child {
      border-radius: 0 $btn-border-radius $btn-border-radius 0;
    }

    &:first-child:last-child {
      border-radius: $btn-border-radius;
    }

    &:hover {
      position: relative;
      color: $primary-color;
    }

    .#{$radio-prefix-cls}-inner {
      // opacity: 0;
      display: none;
      width: 0;
      height: 0;
    }
    input {
      opacity: 0;
      width: 0;
      height: 0;
    }

    &-checked {
      background: $radio-group-button-wrapper-checked-color;
      border-color: $primary-color;
      color: $primary-color;
      box-shadow: -1px 0 0 0 $primary-color;

      &:first-child {
        border-color: $primary-color;
        box-shadow: none !important;
      }

      &:hover {
        border-color: lighten($primary-color, 10%);
        box-shadow: -1px 0 0 0 tint($primary-color, 20%);
        color: lighten($primary-color, 20%);
      }

      &:active {
        border-color: shade($primary-color, 5%);
        box-shadow: -1px 0 0 0 shade($primary-color, 5%);
        color: darken($primary-color, 5%);
      }
    }

    &-disabled {
      border-color: $border-color-base;
      background-color: $background-color-base;
      cursor: $cursor-disabled;
      color: $radio-group-button-wrapper-disabled-color;

      &:first-child,
      &:hover {
        border-color: $border-color-base;
        background-color: $background-color-base;
        color: $radio-group-button-wrapper-disabled-hover-color;
      }
      &:first-child {
        border-left-color: $border-color-base;
      }
    }

    &-disabled.#{$radio-prefix-cls}-wrapper-checked {
      color: $radio-group-button-wrapper-disabled-wrapper-checked-color;
      background-color: $radio-group-button-wrapper-disabled-wrapper-checked-background-color;
      border-color: $border-color-base;
      box-shadow: none !important;
    }
    // btn 带按钮--o45
    .#{$radio-prefix-cls}-btnIcon.#{$radio-prefix-cls} {
      width: $radio-group-button-wrapper-btnIcon-width;
      .#{$radio-prefix-cls}-inner {
        // opacity: 1;
        display: inline-block;
        width: $radio-group-button-wrapper-btnIcon-inner-width;
        height: $radio-group-button-wrapper-btnIcon-inner-height;
      }
    }
  }
}

.#{$radio-group-button-prefix-cls}.#{$radio-group-prefix-cls}-large
  .#{$radio-prefix-cls}-wrapper {
  height: $btn-circle-size-large;
  line-height: $btn-circle-size-large - 2px;
  font-size: $font-size-small;
  .#{$radio-prefix-cls}-btnIcon.#{$radio-prefix-cls} {
    width: $radio-group-button-large-btnIcon-width;
    .#{$radio-prefix-cls}-inner {
      // opacity: 1;
      display: inline-block;
      width: $radio-group-button-large-btnIcon-inner-width;
      height: $radio-group-button-large-btnIcon-inner-height;
    }
  }
}

.#{$radio-group-button-prefix-cls}.#{$radio-group-prefix-cls}-small
  .#{$radio-prefix-cls}-wrapper {
  height: $btn-circle-size-small;
  line-height: $btn-circle-size-small - 2px;
  padding: $radio-group-button-small-wrapper-padding;
  font-size: $font-size-small;
  &:first-child {
    border-radius: $btn-border-radius-small 0 0 $btn-border-radius-small;
  }
  &:last-child {
    border-radius: 0 $btn-border-radius-small $btn-border-radius-small 0;
  }
  .#{$radio-prefix-cls}-btnIcon.#{$radio-prefix-cls} {
    width: 16px;
    .#{$radio-prefix-cls}-inner {
      // opacity: 1;
      display: inline-block;
      width: $radio-group-button-small-btnIcon-inner-width;
      height: $radio-group-button-small-btnIcon-inner-height;
    }
  }
}

.#{$form-item-prefix-cls}-error {
  .#{$radio-prefix-cls} {
    @include radio-error();
    &-icon {
      color: $error-color;
    }
  }
}
